.products {
  padding-top: 121px;
  padding-bottom: 98px;
}

.products__label {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: calc(21 / 18);
  text-align: center;
  text-transform: uppercase;
  color: #ffa5ba;
  margin-bottom: 15px;
}
.products__title {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 39px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #d41443;
  margin-bottom: 31px;
}
.products__cards {
  align-content: center;
  width: 280px;
  height: auto;
  overflow: hidden;
  padding-top: 113px;
}

.products__cards-item {
  padding-top: 161px;
  width: 280px;
  background: #ffb8ca;
  border-radius: 24px;
  margin-bottom: 171px;
  padding-bottom: 30px;
  position: relative;
  width: 100%;
}
.second {
  background: #f0d1a5;
}
.third {
  background: #c2e297;
}

.products__button {
  display: block;
  width: 40px;
  height: 40px;
  margin-left: auto;
  margin-right: auto;
  border: none;
  background-color: transparent;
  border-radius: 50%;
}
.products__button::before {
  content: url(../images/products/arrow.png);
  display: flex;
  align-items: center;
  justify-content: center;
}

.test {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.products__text {
  display: block;
  max-width: 200px;
  margin-left: auto;
  margin-right: auto;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 152%;
  text-align: center;
  letter-spacing: 0.04em;
  color: #ffffff;
  margin-bottom: 30px;
}

.products__cards-title::before {
  content: url(../images/products/home-ice1.png);
  width: 249px;
  height: 252px;
  position: absolute;
  left: 7%;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(-115%);
}
.products__cards2-title::before {
  content: url(../images/products/home-ice2.png);
  width: 249px;
  height: 252px;
  position: absolute;
  left: 7%;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(-115%);
}
.products__cards3-title::before {
  content: url(../images/products/home-ice3.png);
  width: 249px;
  height: 252px;
  position: absolute;
  left: 7%;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(-115%);
}

.products__cards-title::after {
  content: url(../images/products/group.png);
  background-repeat: no-repeat;
  width: 80%;
  height: 252px;
  position: absolute;
  display: block;
  left: 10%;
}
.products__cards2-title::after {
  content: url(../images/products/group.png);
  background-repeat: no-repeat;
  width: 80%;
  height: 252px;
  position: absolute;
  display: block;
  left: 10%;
}
.products__cards3-title::after {
  content: url(../images/products/group.png);
  background-repeat: no-repeat;
  width: 80%;
  height: 252px;
  position: absolute;
  display: block;
  left: 10%;
}

.products__cards-title,
.products__cards2-title,
.products__cards3-title {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: calc(23 / 20);
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  display: block;
  position: relative;
  color: #ffffff;
  margin-bottom: 66px;
}

.products__hover-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(240, 151, 203, 0.8);
  transition-duration: 250ms;
}

.products__hover-overlay-text {
  font-size: 18px;
  line-height: 1.56;
  letter-spacing: 0.03em;
  color: #fff;
}
